import React from 'react';
import PropTypes from 'prop-types';
import { Panel, PanelHeader, HeaderButton, Link,  Button, Div,  platform, IOS, Group, Cell, List} from '@vkontakte/vkui';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import Icon24BrowserForward from '@vkontakte/icons/dist/24/browser_forward';
import Icon24Newsfeed from '@vkontakte/icons/dist/24/newsfeed';
import Icon24User from '@vkontakte/icons/dist/24/user';
import Icon24Recent from '@vkontakte/icons/dist/24/recent';

import Icon24Favorite from '@vkontakte/icons/dist/24/favorite';
const osname = platform();

const House = props => (
	<Panel id="house">
<PanelHeader
      left={<HeaderButton onClick={() => window.history.back()}>
        {osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
      </HeaderButton>}
    >
         
    </PanelHeader>
<Group>
              <List>
                <Cell before={<Icon24Favorite fill="#ffa500"/>} indicator="5.0"> </Cell>
             </List>
            </Group>



<Group>
<List>
<Cell before={<Icon24User fill="#4bb34b" />} indicator={<Link component = "a" href = "https://www.spletenie.ru/users/olga-babich/povarenok/"> </Link>}>  </Cell>
<Cell before={<Icon24Newsfeed fill="#4bb34b" />} indicator={<Link component = "a" href = "https://www.povarenok.ru">povarenok.ru</Link>}> </Cell>
<Cell before={<Icon24Recent fill="#4bb34b" />} indicator="50 "> </Cell>

</List>
</Group> 





<Group>
<Div>:
<br />   4 . .
<br />   4 . .
<br />    2 . 
<br />    9 . .
<br />  .
</Div>
</Group>

         <Group>
<Div>:
<br />  , , .
<br />   - 
<br />   .
<br />    2 . 
<br />     
<br />  , 
<br />  , 
<br />  !
<br />   , 
<br />      . 
<br />     . 
<br />   , 
<br /> .
<br />   , 
<br />    
<br /> .

</Div>
</Group>
	

  </Panel>
);

House.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default House;
